/* colors */
/* main variables */
@import url("https://fonts.googleapis.com/css?family=Poppins:400,600,700");
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700");
footer .logo, .header-top .logo { background: url(../img/logo.png) no-repeat center; background-size: cover; display: block; }

footer .footer-widget a, .navigation__item { display: inline-block; line-height: 2.6rem; padding: .5rem 0; position: relative; }

footer .footer-widget a:after, .navigation__item:after { background-color: #fff; bottom: 0; content: ''; height: 2px; left: 0; margin: auto; position: absolute; right: 0; width: 0; transition: all 0.2s linear; }

footer .footer-widget a:hover, .navigation__item:hover { color: #fff; }

footer .footer-widget a:hover:after, .navigation__item:hover:after { width: 100%; }

*, *:before, *:after { box-sizing: inherit; margin: 0px; padding: 0px; }

html { height: 100%; -webkit-text-size-adjust: none; -ms-text-size-adjust: none; }

body { background-color: #fff; height: 100%; line-height: 1; position: relative; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

/*MAIN*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main { display: block; }

blockquote, q { quotes: none; }

blockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }

table { border-collapse: collapse; border-width: 0px; }

ul { list-style: none; }

input, textarea { color: #333; font-family: "Poppins", sans-serif; outline: none; border-radius: 0; -webkit-appearance: none; }

input[type="button"], input[type="submit"], button { cursor: pointer; }

a { color: #000; transition: all 0.2s linear; text-decoration: none; outline: none; -webkit-tap-highlight-color: transparent; }

a, span, div, button { outline: none !important; }

input[type=submit], input[type=button], button { -webkit-appearance: none; outline: none; }

/* Slider */
.slick-slider { position: relative; display: block; -moz-box-sizing: border-box; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

html { font-size: 62.5%; }

body { color: #000; font-family: "Poppins", sans-serif; font-size: 1.6rem; line-height: 1.7rem; box-sizing: border-box; }

button, select, label[for], input[type='submit'], input[type='image'] { color: inherit; cursor: pointer; outline: none !important; }

input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button { -webkit-appearance: none; margin: 0; }

input[type='number'] { -moz-appearance: textfield; }

input::-ms-clear { display: none; }

:disabled, [disabled], [aria-disabled="true"] { cursor: not-allowed; }

a { color: inherit; text-decoration: none; }

a:hover { text-decoration: none; }

strong { font-weight: 700; }

b { font-weight: 600; }

input, select, button, textarea { font: inherit; outline: none; }

img { height: auto; max-width: 100%; }

/*** PRE-DEFINED CLASSES ***/
.inline { display: inline-block; }

.hidden { display: none; }

.disabled { pointer-events: none; }

.pull-left { float: left; }

.pull-right { float: right; }

.clearfix:after { content: ""; display: table; clear: both; }

header { background: #656770; color: #fff; height: 75rem; order: 1; padding-top: 11rem; width: 100%; }

@media screen and (max-width: 425px) { header { height: 60rem; } }

header > .wrapper { height: 100%; display: flex; flex-direction: column; align-items: flex-start; justify-content: center; }

header h1 { max-width: 46rem; margin-bottom: 7.5rem; }

header .btn { border-width: 1px; letter-spacing: 3px; padding: 3rem 5rem; text-transform: uppercase; }

header .btn:hover { color: #2d2d43; }

/*
================================================================================
|                                     FOOTER                                   |
================================================================================
*/
footer { background: #4c4e5a; color: #e6e5e5; font-family: "Lato", sans-serif; line-height: 3rem; order: 3; }

footer .wrapper { display: flex; justify-content: space-between; padding: 8rem 0 7rem; }

@media screen and (max-width: 767px) { footer .wrapper { flex-wrap: wrap; justify-content: space-evenly; padding: 5rem 0 4rem; } }

footer .footer-widget { margin: 0 3rem; max-width: 27rem; }

@media screen and (max-width: 767px) { footer .footer-widget:not(:last-child) { margin-bottom: 2rem; } }

footer .footer-widget:first-child { font-weight: 300; }

@media screen and (max-width: 767px) { footer .footer-widget:first-child { align-items: center; display: flex; max-width: 100%; } }

footer .footer-widget .widget__title { color: #fff; font-family: "Poppins", sans-serif; font-size: 2rem; font-weight: 600; margin-bottom: 1.5rem; }

footer p { line-height: 2.6rem; padding: .5rem 0; }

footer .logo { height: 6rem; margin: 0 2rem .2rem 0; min-width: 5.3rem; width: 5.3rem; }

footer .logo:after { content: none !important; }

footer .copyright { background: #404250; color: #b6b5b9; display: flex; align-items: center; justify-content: center; height: 8rem; min-width: 320px; line-height: 3.6rem; text-align: center; width: 100%; }

.main-wrapper { display: flex; flex-direction: column; justify-content: flex-start; min-height: 100%; min-width: 320px; overflow: hidden; padding: 0; position: relative; width: 100%; }

.content { background: #fff; flex-grow: 1; min-width: 320px; order: 2; text-align: left; width: 100%; }

.wrapper { margin: 0 auto; max-width: 1230px; min-width: 320px; padding: 0 3rem; position: relative; width: 100%; }

/* titles */
h1 { font-size: 6rem; line-height: 8rem; color: #fff; font-weight: 600; padding: 1.5rem 0; }

@media screen and (max-width: 425px) { h1 { font-size: 4rem; line-height: 6rem; } }

h2 { font-size: 3.6rem; line-height: 4.8rem; color: #2d2d43; font-weight: 600; padding: 1.5rem 0; }

h3 { font-size: 2.4rem; line-height: 6rem; color: #2d2d43; font-weight: 600; }

h4 { font-size: 1.6rem; line-height: 6rem; color: #696969; font-family: "Lato", sans-serif; font-weight: 400; letter-spacing: .3rem; margin-bottom: 2.5rem; text-transform: uppercase; position: relative; text-align: center; }

h4:after { border: 1px solid; bottom: 0; content: ''; height: .2rem; margin: auto; position: absolute; width: 3.7rem; }

h4.center:after { left: 0; right: 0; }

h4.left { text-align: left; }

h4.left:after { left: 0; }

h4.right { text-align: right; }

h4.right:after { right: 0; }

/* text position */
.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

section:nth-child(odd) { background-color: #f7f7f7; }

.btn { background: none; border: 2px solid #fff; font-family: "Lato", sans-serif; font-weight: 700; padding: 1.5rem 3rem; position: relative; text-decoration: none; transition: all 0.2s linear; white-space: nowrap; }

.btn:hover { background: #fff; color: #4451fc; }

.header-top { background: #656770; height: 11rem; position: fixed; left: 0; top: 0; width: 100%; z-index: 10; }

.header-top .wrapper { align-items: center; display: flex; justify-content: space-between; height: 100%; }

.header-top .logo { flex-shrink: 0; height: 7rem; width: 6rem; }

@media screen and (max-width: 425px) { .header-top .logo { display: none; } }

.header-top .social-links { display: flex; align-items: center; }

.header-top .social-links a { margin-left: 1.3rem; }

.header-top .btn { background: #fff; font-family: "Poppins", sans-serif; font-weight: 600; font-size: 1.3rem; color: #2d2d43; letter-spacing: .5px; margin-left: 2rem !important; padding: 1.5rem 2.5rem; }

.header-top .btn:hover { background: none; color: #fff; }

@media screen and (max-width: 768px) { .navigation { margin: 0 auto 0 3rem; } }

@media screen and (max-width: 425px) { .navigation { margin: 0; } }

.navigation__checkbox { display: none; }

.navigation__button { align-items: center; background: #fff; display: flex; justify-content: center; height: 4.8rem; position: relative; width: 4.8rem; }

@media screen and (min-width: 769px) { .navigation__button { display: none; } }

.navigation__icon { position: relative; }

.navigation__icon:before, .navigation__icon:after { content: ""; position: absolute; left: 0; transition: all 0.2s linear; }

.navigation__icon:before { top: -.8rem; }

.navigation__icon:after { top: .8rem; }

.navigation__icon, .navigation__icon:before, .navigation__icon:after { width: 3rem; height: 2px; background-color: #333; display: inline-block; }

.navigation__checkbox:checked + .navigation__button .navigation__icon { background-color: transparent; }

.navigation__checkbox:checked + .navigation__button .navigation__icon:before { top: 0; transform: rotate(135deg); }

.navigation__checkbox:checked + .navigation__button .navigation__icon:after { top: 0; transform: rotate(-135deg); }

@media screen and (max-width: 768px) { .navigation__list { align-items: flex-end; background: rgba(255, 255, 255, 0.95); background: rgba(0, 0, 0, 0.95); bottom: 0; display: flex; flex-direction: column; justify-content: center; left: 0; opacity: 0; overflow: auto; padding: 2rem 0; position: fixed; right: 0; top: 11rem; transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55); width: 0; } }

.navigation__checkbox:checked ~ .navigation__list { opacity: 1; padding: 2rem; width: 100%; }

.navigation li { display: inline-block; padding: 0 2rem; }

@media screen and (max-width: 1024px) { .navigation li { padding: 0 1rem; } }

@media screen and (max-width: 768px) { .navigation__item { padding: 1.5rem 0; } }

.navigation .fas { font-size: 1rem; }

.service { background: linear-gradient(#656770 50%, #fff 0); color: #fff; }

.service .wrapper { background-color: #4451fc; color: #fff; display: flex; justify-content: space-evenly; margin: 0; max-width: 80.4687%; padding: 10rem 1rem 4rem; }

@media screen and (max-width: 1023px) { .service .wrapper { max-width: 100%; } }

@media screen and (max-width: 768px) { .service .wrapper { flex-direction: column; padding: 5rem 1rem 0; } }

.service .item { display: flex; max-width: 33rem; margin: 0 1rem 5rem; }

@media screen and (max-width: 768px) { .service .item { max-width: 100%; } }

.service .item__image { flex-shrink: 0; margin-right: 3rem; }

.service .item__title { color: #fff; font-weight: 400; line-height: 3rem; margin-bottom: 1.2rem; }

.service .item__text { font-family: "Lato", sans-serif; font-weight: 300; line-height: 3.4rem; }

.about { padding: 9rem 0 7rem; text-align: center; }

@media screen and (max-width: 768px) { .about { padding: 5rem 0 3rem; } }

.about h2 { max-width: 57rem; margin: 0 auto 3.5rem; width: 100%; }

.about__items { display: flex; justify-content: center; }

@media screen and (max-width: 660px) { .about__items { align-items: center; flex-direction: column; } }

.about .item { display: flex; flex-direction: column; margin: 0 3rem 2rem; width: 28rem; }

.about .item__image { align-items: center; display: flex; flex: 1; justify-content: center; margin-bottom: 2.5rem; }

.about .item__title { color: #2d2d43; font-size: 1.8rem; font-weight: 600; margin-bottom: 2rem; }

.about .item__text { color: #696969; font-family: "Lato", sans-serif; font-weight: 300; line-height: 3rem; }

.works { padding: 9.5rem 0 9rem 0; }

.works h2 { max-width: 46rem; width: 100%; }

.clients { padding: 10rem 0; }

@media screen and (max-width: 768px) { .clients { padding: 5rem 0; } }

.clients__list { display: flex; justify-content: space-between; flex-wrap: wrap; }

@media screen and (max-width: 600px) { .clients__list { justify-content: center; } }

.clients__list li { margin: 2rem; min-width: 11.5rem; text-align: center; }

.contact { background: linear-gradient(to right, #4451fc 50%, transparent 0), linear-gradient(#fff 50%, #4c4e5a 0); }

.contact .wrapper { align-items: flex-start; background-color: #4451fc; color: #fff; display: flex; padding-top: 9.5rem; padding-bottom: 11rem; padding-left: 6rem; right: 3rem; }

@media screen and (max-width: 1024px) { .contact .wrapper { justify-content: space-between; } }

@media screen and (max-width: 767px) { .contact .wrapper { padding-bottom: 6rem; padding-top: 6rem; } }

@media screen and (max-width: 650px) { .contact .wrapper { flex-direction: column; } }

@media screen and (max-width: 425px) { .contact .wrapper { padding-left: 3rem; right: 0; } }

.contact .wrapper > div { max-width: 27rem; margin-right: 13rem; min-width: 24rem; }

@media screen and (max-width: 1024px) { .contact .wrapper > div { margin-right: 0; } }

@media screen and (max-width: 650px) { .contact .wrapper > div { max-width: 100%; } }

.contact h2, .contact h4 { color: #fff; }

.contact .form { font-family: "Lato", sans-serif; display: flex; justify-content: space-between; flex-wrap: wrap; max-width: 57rem; width: 100%; }

.contact .form__group { max-width: 27rem; margin-bottom: 1rem; width: 100%; }

.contact .form__group:last-of-type { max-width: 100%; }

.contact .form__label { display: block; font-size: 1.4rem; margin-top: .7rem; transition: all 0.2s linear; width: 100%; }

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) { .contact .form__label { visibility: hidden; } }

@supports (display: -ms-grid) { .contact .form__label { visibility: hidden; } }

.contact .form__input { background: none; border: none; border-bottom: 1px solid #fff; color: #fff; padding: 1.3rem 0; width: 100%; }

.contact .form__input::-webkit-input-placeholder { color: #fff; }

.contact .form__input:-moz-placeholder { color: #fff; }

.contact .form__input::-moz-placeholder { color: #fff; }

.contact .form__input:-ms-input-placeholder { color: #fff; }

.contact .form__input:placeholder-shown + .form__label { opacity: 0; visibility: hidden; transform: translateY(-4rem); }

.contact .form textarea { resize: none; width: 100%; }

.contact .form .btn { margin-top: 1rem; }
